<!--  -->
<template>
  <div class="cart-item">
      <div class="correct" @click="check()"><img src="~assets/img/shoppingcar/correct.svg" :class="{correct_img:cartlist.checked}" ></div>
      <div class="product-img"><img :src="cartlist.image" @load="completeloading"></div>
      <div class="detail">
        <p>{{cartlist.title}}</p>
        <p>{{cartlist.desc}}</p>
        <div class="bottom">
            <span>{{cartlist.price}}</span>
            <span>X{{cartlist.count}}</span>
        </div>
      </div>
  </div>
</template>

<script>

export default {
 name:'CartItem',
  data () {
    return {
    };
  },
  props:{
      cartlist:{
          type:Object,
          default(){
              return {}
          }
      }
  },

  methods:{
      check(){
          this.cartlist.checked = !this.cartlist.checked
          console.log(this.$store.state.shoppingcar)
      },
      completeloading(){
          this.$emit("loadingshoppingcar")
      }
  }
}
</script>
<style scoped>
.cart-item{
    width: 100%;
    height: 150px;
    margin-top:10px;
    border-bottom: 1px solid rgb(167, 162, 162);
    display: flex;
}
.correct{
    flex:1;
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
}
.correct>img{
    width:20px;
    height: 20px;
    
    border: 1px solid rgb(177, 171, 171);
    border-radius:50%;
}
.correct_img{
    background: red;
}
.product-img{
    flex:3;
}
.product-img>img{
    width: 100px;
    height: 140px;
    border-radius: 5px;
}
.detail{
    flex:6;
    display: flex;
    flex-direction: column;
    padding:10px;
    justify-content: space-between;

}
.detail>p:nth-of-type(1){
    display: inline-block;
    width: 200px;
    font-size: 17px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:nowrap;
}
.detail>p:nth-of-type(2){
    display: inline-block;
    width: 200px;
    font-size: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:nowrap;
}
.bottom{
   display: flex;
   justify-content: flex-start;    
}
.bottom>span:nth-of-type(1){
    flex:9;
    color:red;
}
.bottom>span:nth-of-type(2){
    flex:1;
}
</style>