<!--  -->
<template>
  <div class="cart-list">
    <scroll  class="tall" ref="scroll">
     <cart-item v-for="(item,index) in cartlists" :key="index" :cartlist="item" @loadingshoppingcar="loadingshoppingcar">
     </cart-item>
    </scroll>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Scroll from "components/common/Scroll/Scroll.vue";

import CartItem from "./CartItem.vue"
export default {
  name: "CartList",
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["cartlists"]),
  },
  methods:{
    loadingshoppingcar(){
      this.$refs.scroll.refresh()
    }
  },
   components: { Scroll,CartItem},
};
</script>
<style scoped>
.cart-list{
  height: 100%;
}
.tall{
  position:absolute;
  top:44px;
  bottom:47px;
  width: 100%;
  overflow: hidden;
}
</style>