<!--  -->
<template>
  <div class="cart-bottom-bar">
       <div class="correct" @click="check()"><img src="~assets/img/shoppingcar/correct.svg" > <span>全选</span></div>
       <span>合计:￥{{totalPrice}}</span>
       <span>去计算:{{totalCount}}</span>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
 name:'CartBottomBar',
  data () {
    return {
    };
  },
  computed:{
      ...mapGetters(["cartlists"]),
      totalPrice(){
          return this.cartlists.filter(item=>item&&item.checked===true).reduce((pv,item)=>{
             return pv + Number(item.price.substring(1,item.price.length-1))*item.count 
          },0)
      },
      totalCount(){
          return this.cartlists.filter(item=>item&&item.checked ===true).reduce((pv,item)=>{
              return item.count + pv
          },0)
      }
  }
}
</script>
<style scoped>
.cart-bottom-bar{
    height: 50px;
    width: 100%;
    position: fixed;
    bottom:47px;
    background: #e8e8e8;
    display: flex;
    justify-content: space-between;
}
.cart-bottom-bar>span{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cart-bottom-bar>span:nth-of-type(1){
    font-size: 15px;
}
.cart-bottom-bar>span:nth-of-type(2){
    font-size: 18px;
    color:white;
    background: red;
    padding-right: 20px;
    
}
.correct{
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
    padding-left: 20px;
}
.correct>img{
    width:20px;
    height: 20px;
    
    border: 1px solid rgb(177, 171, 171);
    border-radius:50%;
}
.correct_img{
    background: red;
}
</style>